



















import {Component, Prop, Vue} from 'vue-property-decorator'

@Component({
  model: {
    prop: 'page',
    event: 'change'
  }
})
export default class Pagination extends Vue {
  @Prop() private page!: number
  @Prop() private pages!: number
  @Prop() private total!: number

  go(page: number) {
    this.$emit('change', page)
  }
}
